import React from "react";

const PlusIconBtn = ({ onClick, className, ...props }) => {
  return (
    <button
      {...props}
      onClick={onClick}
      className={`${className} w-6 h-6 bg-[#844FBA] text-white p-1 rounded-full flex items-center justify-center text-2xl focus:ring`}
    >
      <span className="pb-[4px]">+</span>
    </button>
  );
};

export default PlusIconBtn;
