import React from "react";
import OverlayPopup from "../Utilities/OverlayPopup";
import BoxIcon from "../../../assets/svgs/fill-box.svg";

export default function CategoryPagePopup({ show_popup, set_show_popup }) {
  return (
    <OverlayPopup show_popup={show_popup} set_show_popup={set_show_popup}>
      <div className="w-full lg:w-[800px] bg-[#F2F2F2] rounded relative z-50 p-6 space-y-5">
        <img
          className="w-full"
          src="https://i.ibb.co/B6dpbWt/popup.png"
          alt=""
        />

        <h2 className="text-2xl font-semibold text-[#222]">
          1. Muneland Workshop
        </h2>

        <div className="border-2 border-[#D9D9D9] rounded-lg p-4 space-y-4">
          <div>
            <span>Zone text 1</span>
            <input
              type="text"
              className="bg-white w-full h-[45px] rounded focus:ring"
            />
          </div>
          <div>
            <span>Zone text 2</span>
            <input
              type="text"
              className="bg-white w-full h-[45px] rounded focus:ring"
            />
          </div>
          <div>
            <span>Zone text 3</span>
            <input
              type="text"
              className="bg-white w-full h-[45px] rounded focus:ring"
            />
          </div>
          <div>
            <span>Zone text 4</span>
            <input
              type="text"
              className="bg-white w-full h-[45px] rounded focus:ring"
            />
          </div>
        </div>

        <div className="flex flex-col justify-center items-center gap-2">
          <img src={BoxIcon} alt="" />
          <span className="font-semibold">Put it in your box</span>
        </div>
      </div>
    </OverlayPopup>
  );
}
