import React from "react";

const ChatSingleMessage = ({ image, name, time, message }) => {
  return (
    <div className="flex items-start gap-4">
      <div className="min-w-[60px] flex items-start justify-center">
        <img
          className="min-w-[60px] max-w-[60px] rounded-full"
          src={image}
          alt=""
        />
      </div>

      <div className="flex flex-col">
        <div className="flex items-center justify-start gap-12">
          <h3 className="text-xl font-semibold">{name}</h3>
          <span className="text-[#777]">{time}</span>
        </div>
        <p className="text-base leading-6">{message}</p>
      </div>
    </div>
  );
};

export default ChatSingleMessage;
