import React from "react";

const OutlineButton = ({
  children,
  onClick,
  type = "button",
  className,
  ...props
}) => {
  return (
    <button
      {...props}
      type={type}
      onClick={onClick}
      className={`w-full h-[48px] border border-[#999] rounded flex items-center justify-center gap-2 focus:ring focus:border-transparent ${className}`}
    >
      {children}
    </button>
  );
};

export default OutlineButton;
