import React from "react";
import CheckGreen from "../../../assets/svgs/check-green.svg";
import TimesOutline from "../../../assets/svgs/times-outline.svg";
import Clock from "../../../assets/svgs/clock.svg";

const QuizHeader = ({
  total_questions,
  total_done,
  total_correct,
  total_wrong,
}) => {
  return (
    <div className="bg-[#F2F2F7] w-full lg:w-[800px] h-fit py-6 md:py-0 md:h-[80px] flex items-start md:items-center justify-between px-5 lg:px-10 rounded flex-col gap-6 md:gap-0 md:flex-row sticky top-0 z-50">
      <div className="flex items-center gap-3">
        <p className="font-semibold">
          Questions {total_done}/{total_questions}
        </p>
        <div className="w-28 h-2.5 rounded-full bg-gray-300 overflow-hidden relative">
          <div
            style={{
              width: `${(100 * total_done) / total_questions}%`,
            }}
            className="h-2.5 bg-blue-600 absolute left-0 inset-y-0"
          />
        </div>
      </div>

      <div className="flex items-center gap-3">
        <p className="font-semibold">You Score</p>
        <p className="flex items-center gap-1">
          <img src={CheckGreen} alt="" /> {total_correct}
        </p>
        <p className="flex items-center gap-1">
          <img src={TimesOutline} alt="" /> {total_wrong}
        </p>
      </div>

      <div className="flex items-center gap-2">
        <img src={Clock} alt="" />
        <p className="text-gray-500 font-semibold">Time: 29:59 </p>
      </div>
    </div>
  );
};

export default QuizHeader;
