import React from "react";
import HeaderWithPhoto from "../components/Headers/HeaderWithPhoto";
import PaymentConfirmLeftbar from "../components/PaymentConfirm/PaymentConfirmLeftbar";
import PaymentConfirmRightbar from "../components/PaymentConfirm/PaymentConfirmRightbar";

const PaymentConfirm = () => {
  return (
    <>
      <HeaderWithPhoto />
      <div className="w-full bg-[#F2F2F7] text-[#222] min-h-screen relative adjust_bg">
        <div className="x_container  flex flex-col lg:flex-row lg:items-stretch">
          {/* Left Sidebar */}
          <PaymentConfirmLeftbar />

          {/* Right Sidebar */}
          <PaymentConfirmRightbar />
        </div>
      </div>
    </>
  );
};

export default PaymentConfirm;
