import React, { useState } from "react";
import CardGrids, { Card } from "../../components/Categories/CardGrids";
import CategoryPagePopup from "../../components/Categories/CategoryPagePopup";

import PlusIconBtn from "../../components/Utilities/PlusIconBtn";

export const solution_playbook_data = [
  {
    image: "https://i.ibb.co/cgkDxZc/Ent-Solution-Playbook-4.png",
    text: "1. Capability Assessment & Requirements",
  },
  {
    image: "https://i.ibb.co/mbRkxY6/Strategy-Vision-7.png",
    text: "2. Co-Op & Market Analysis",
  },
  {
    image: "https://i.ibb.co/LnchTS6/Ent-Solution-Playbook-2.png",
    text: "3.Tech., Ecosystem, & V/A Requirements",
  },
  {
    image: "https://i.ibb.co/FWnrpcS/Ent-Solution-Playbook-1.png",
    text: "4. Funding Model Process & Governance",
  },
];

const EntSolutionPlaybook = () => {
  const [show_popup, set_show_popup] = useState(false);
  function card_click_handler() {
    set_show_popup(true);
  }

  return (
    <>
      <h3 className="text-2xl font-semibold">Ent. Solution Playbook</h3>
      <br />
      <div className="bg-white p-10 rounded">
        <div className="flex justify-between gap-5 flex-col sm:flex-row">
          <div className="w-full">
            <h3 className="font-semibold mb-2 text-2xl text-black">
              What is it ?
            </h3>
            <p className="text-lg text-black leading-6">
              We test and iterate the solution, integrating business value,
              technology, automation and experience.
            </p>
          </div>
          <div className="w-full">
            <h3 className="font-semibold mb-2 text-2xl text-black">
              Why do it ?
            </h3>
            <p className="text-lg text-black leading-6">
              To ensure the product or service meets user needs and delivers
              360 value for the client.
            </p>
          </div>
        </div>

        <div className="py-10">
          <h3 className="font-semibold mb-2 text-2xl text-black">
            Activities and Outcomes forEnt. Solution Playbook
          </h3>
          <p className="text-lg font-[#777]">
            Please complete the cards that best describe your services.
          </p>
        </div>

        <CardGrids>
          {solution_playbook_data.map((d, i) => (
            <Card
              key={i}
              onClick={card_click_handler}
              className="cursor-pointer"
            >
              <div className="relative rounded overflow-hidden h-full">
                <img className="w-full" src={d.image} alt="" />
                <div className="absolute top-2 right-2">
                  <PlusIconBtn />
                </div>
              </div>
              <p className="text-[#222] text-sm font-semibold pt-5 h-16 flex items-center">
                {d.text}
              </p>
            </Card>
          ))}
        </CardGrids>
      </div>

      <CategoryPagePopup
        show_popup={show_popup}
        set_show_popup={set_show_popup}
      />
    </>
  );
};

export default EntSolutionPlaybook;
