import React from "react";
import SingleQuestion from "./SingleQuestion";

const QuizBox = ({ quiz_category, quizs }) => {
  return (
    <div className="text-[#222]">
      <h3
        style={{ fontSize: "34px" }}
        className="font-semibold mb-2 capitalize"
      >
        {quiz_category} Questions
      </h3>
      <div className="bg-[#F2F2F7] rounded] p-9 pb-0 flex flex-col gap-9">
        {quizs?.map((quiz, i) => (
          <SingleQuestion key={i} quiz={quiz} />
        ))}
      </div>
    </div>
  );
};

export default QuizBox;
