import React, { useEffect, useState } from "react";
import HeaderWithPhoto from "../components/Headers/HeaderWithPhoto";
import QuizBox from "../components/QuizQuestions/QuizBox";
import QuizHeader from "../components/QuizQuestions/QuizHeader";
import BackLinkButton from "../components/Utilities/BackLinkButton";
import all_quiz_questions from "../../data/quiz-questions";
import BottomToTop from "../components/Utilities/BottomToTop";

import { useSelector, useDispatch } from "react-redux";
import { Navigate } from "react-router-dom";

const QuizQuestions = () => {
  const { total_questions, total_done, total_correct, total_wrong } =
    useSelector((state) => state.quiz);

  const dispatch = useDispatch();

  const [all_done, set_all_done] = useState(false);

  useEffect(() => {
    // Set Total Questions
    let total = 0;
    Object.values(all_quiz_questions).map(
      (questions) => (total += questions.length)
    );
    dispatch({ type: "set_total_questions", payload: total });

    // If All Done
    if (total_done > 0 && total_done === total_questions) {
      set_all_done(true);
    }
    // If All Done

    if (all_done) {
      const correct_percentage = Math.round((100 * total_correct) / total_done);

      dispatch({
        type: "set_correct_percentage",
        payload: correct_percentage,
      });
    }
  }, [total_questions, total_done, total_correct, all_done, dispatch]);

  if (all_done) {
    const correct_percentage = Math.round((100 * total_correct) / total_done);
    if (correct_percentage > 60) return <Navigate to="/congrats" />;
    return <Navigate to="/try-again" />;
  }

  return (
    <>
      <HeaderWithPhoto />

      <section style={{ minHeight: "calc(100vh - 75px)" }}>
        <div className="x_container py-10 flex flex-col gap-6 lg:gap-0 lg:flex-row items-start justify-between">
          <BackLinkButton />

          <div>
            <QuizHeader
              total_questions={total_questions}
              total_done={total_done}
              total_correct={total_correct}
              total_wrong={total_wrong}
            />
            <br />
            <div className="w-full lg:w-[710px] flex flex-col gap-12">
              <h2
                style={{ fontSize: "40px" }}
                className="text-[#222] font-semibold"
              >
                Take a Quiz to confirm your skillset
              </h2>

              {/* quiz */}
              {Object.keys(all_quiz_questions).map((key) => (
                <QuizBox
                  key={key}
                  quiz_category={key}
                  quizs={all_quiz_questions[key]}
                />
              ))}
              {/* quiz */}
            </div>
          </div>
          <div className="w-10 hidden lg:block" />
        </div>
      </section>

      <BottomToTop />
    </>
  );
};

export default QuizQuestions;
