import React, { useState } from "react";
import PaymentRightbarHeader from "../Payment/PaymentRightbarHeader";
import DarkButtonCircle from "../Utilities/DarkButtonCircle";
import { Navigate } from "react-router-dom";

const PaymentConfirmRightbar = () => {
  const [go_success, set_go_success] = useState(false);
  function confirm_payment(e) {
    set_go_success(true);
  }

  if (go_success) {
    return <Navigate to="/payment-success" />;
  }

  return (
    <div className="w-full pt-6 pb-9 relative z-20">
      <div className="w-full sm:w-[90%] lg:w-[95%] xl:w-[90%] 2xl:w-[600px] mx-auto lg:mx-0 lg:ml-auto 2xl:mx-auto space-y-8">
        <PaymentRightbarHeader />

        <div className="space-y-2">
          <h3 className="text-2xl font-semibold">Complete Order</h3>
          <p className="text-[#777] text-lg leading-5">
            Confirm your purchase details here
          </p>
        </div>

        <div className="space-y-2">
          <h3 className="text-xl font-semibold">Course Details</h3>
          <div className="space-y-1">
            <p className="text-lg">Metaverse Measurement/KPI Strategy</p>
            <p className="text-lg">5 days Delivery</p>
          </div>
        </div>

        {/* Amount Details Footer */}
        <div className="space-y-2">
          <div className="flex justify-between">
            <span className="text-lg">Subtotal</span>
            <span>$ 50</span>
          </div>
          <div className="flex justify-between">
            <span className="text-lg">Vat (20%)</span>
            <span>$ 10</span>
          </div>
          <div className="flex justify-between">
            <b className="text-lg">Total</b>
            <b>$ 50</b>
          </div>
        </div>

        {/* Pay Button */}
        <DarkButtonCircle onClick={confirm_payment}>Pay $60</DarkButtonCircle>
      </div>
    </div>
  );
};

export default PaymentConfirmRightbar;
