import React, { useEffect, useState } from "react";
import HeaderWithPhoto from "../components/Headers/HeaderWithPhoto";
import Metaverse from "../../assets/svgs/metaverse.svg";
import Blockchain from "../../assets/svgs/blockchain.svg";
import Nft from "../../assets/svgs/nft.svg";
import DragableWidthChange from "../components/Utilities/DragableWidthChange";
import { Navigate } from "react-router-dom";
import DarkButtonCircle from "../components/Utilities/DarkButtonCircle";
import SingleRate from "../components/RateSkill/SingleRate";
import BackLinkButton from "../components/Utilities/BackLinkButton";

const RateSkill = () => {
  const [redirect, set_redirect] = useState(false);

  // Here is the percentages
  const [metaverse_percent, set_metaverse_p] = useState(0);
  const [blockchain_percent, set_blockchain_p] = useState(0);
  const [nft_percent, set_nft_p] = useState(0);

  useEffect(() => {
    console.log("metaverse percent %", metaverse_percent);
    console.log("blockchain percent %", blockchain_percent);
    console.log("nft percent %", nft_percent);
  }, [metaverse_percent, blockchain_percent, nft_percent]);

  if (redirect) {
    return <Navigate to="/quiz-questions" />;
  }

  return (
    <>
      <HeaderWithPhoto />
      <section style={{ minHeight: "calc(100vh - 75px)" }}>
        <div className="x_container py-10 flex flex-col gap-6 sm:gap-0 sm:flex-row items-start justify-between">
          <BackLinkButton />

          <div className="w-full sm:w-[450px] flex flex-col gap-12">
            <div>
              <h2 className="text-3xl font-semibold text-center">
                Please rate yourself.
              </h2>
              <br />
              <p className="w-9/12 leading-5 mx-auto text-base text-center text-[#777777]">
                Tell us what is your skill level for each tech for the service
                you think you can provide
              </p>
            </div>

            <div className="w-full flex flex-col gap-12 h-fit p-12 rounded-xl bg-[#F2F2F7]">
              {/* Metaverse */}
              <SingleRate title="Metaverse" Image={Metaverse}>
                <DragableWidthChange get_percentage={set_metaverse_p} />
              </SingleRate>

              {/* Blockchain */}
              <SingleRate title="Blockchain" Image={Blockchain}>
                <DragableWidthChange get_percentage={set_blockchain_p} />
              </SingleRate>

              {/* NFT */}
              <SingleRate title="NFT" Image={Nft}>
                <DragableWidthChange get_percentage={set_nft_p} />
              </SingleRate>
            </div>

            <DarkButtonCircle onClick={() => set_redirect(true)}>
              Continue
            </DarkButtonCircle>
          </div>
          <div />
        </div>
      </section>
    </>
  );
};

export default RateSkill;
