import React from "react";

const Input = ({
  type = "text",
  id,
  name,
  className,
  onChange,
  placeholder,
  ...props
}) => {
  return (
    <input
      {...props}
      onChange={onChange}
      className={`w-full h-[45px] bg-[#F2F2F7] rounded px-4 focus:outline-none focus:ring border placeholder:text-[#777] ${className}`}
      type={type}
      id={id || type}
      name={name || id || type}
      placeholder={placeholder}
    />
  );
};

export default Input;
