import React from "react";
import { Link } from "react-router-dom";
import HeaderWithPhoto from "../components/Headers/HeaderWithPhoto";
import DarkButtonCircle from "../components/Utilities/DarkButtonCircle";
import DarkButtonCircleOutline from "../components/Utilities/DarkButtonCircleOutline";

const Publish = () => {
  return (
    <>
      <HeaderWithPhoto />
      <div
        style={{ minHeight: "calc(100vh - 75px)" }}
        className="x_container flex items-center justify-center py-12"
      >
        <div className="bg-[#F8F8F9] text-[#222] flex flex-col gap-9  w-full sm:w-[500px] p-10">
          <img
            className="w-44 mx-auto"
            src="https://i.ibb.co/C7XRd1q/rocket.png"
            alt=""
          />
          <h2 className="text-4xl text-center font-semibold ">Published</h2>
          <p className="text-xl text-center">
            You have published your services
          </p>
          <div className="flex gap-5">
            <Link to="/freelancer-home" className="block w-full">
              <DarkButtonCircleOutline>Home</DarkButtonCircleOutline>
            </Link>
            <Link to="/freelancer-profile" className="block w-full">
              <DarkButtonCircle>View Profile</DarkButtonCircle>
            </Link>
          </div>
        </div>
      </div>
    </>
  );
};

export default Publish;
