import React from "react";

import RocketSend from "../../../assets/svgs/rocket-right.svg";
import FileIcon from "../../../assets/svgs/chat-files.svg";

const ChatMessagesFooter = () => {
  function handle_submit(e) {
    e.preventDefault();
  }

  return (
    <footer className="px-7 h-16 bg-white">
      <form onSubmit={handle_submit} className="w-full relative -mt-6">
        <input
          className="w-full h-16 focus:outline-none border-2 border-transparent ring-1 focus:ring-transparent focus:border-blue-400 rounded px-5 xl:px-7 transition-none shadow focus:shadow-lg"
          placeholder="Write a message..."
        />
        <div className="flex gap-3 h-full items-center absolute inset-y-0 right-5 my-auto">
          <button>
            <img className="h-5" src={FileIcon} alt="" />
          </button>
          <button>
            <img className="h-5" src={RocketSend} alt="" />
          </button>
        </div>
      </form>
    </footer>
  );
};

export default ChatMessagesFooter;
