import React from "react";

const SingleRate = ({ title, Image, children }) => {
  return (
    <div className="flex flex-col sm:flex-row items-start gap-4 sm:gap-0 justify-between sm:items-center">
      <div className="flex items-center gap-3">
        <img src={Image} alt="" />
        <span className="font-semibold text-[#222] text-xl">{title}</span>
      </div>
      {children}
    </div>
  );
};

export default SingleRate;
