import React, { useEffect, useState } from "react";

const OverlayPopup = ({ children, show_popup, set_show_popup }) => {
  const [show, set_show] = useState(false);
  useEffect(() => {
    setTimeout(() => {
      set_show(show_popup);
    }, 100);
  }, [show_popup]);

  if (!show_popup) {
    return <></>;
  }

  return (
    <div
      className="fixed inset-0 m-auto w-full h-screen flex items-center justify-center"
      style={{ zIndex: "200" }}
    >
      <div
        onClick={() => set_show_popup(false)}
        className="fixed w-full h-full inset-0 m-auto bg-black bg-opacity-50 z-30"
      />
      <div
        style={{
          maxHeight: "95vh",
          opacity: `${show && "1"}`,
        }}
        className="overflow-y-auto min-w-[95%] lg:min-w-fit max-w-[95vw] rounded overflow-hidden relative z-40 transition-all opacity-70"
      >
        {children}
      </div>
    </div>
  );
};

export default OverlayPopup;
