import CheckOutline from "../../../assets/svgs/check-outline.svg";
import CheckShadow from "../../../assets/svgs/check-shadow.svg";

export default function PaymentRightbarHeader() {
  return (
    <div className="w-full px-2 xl:px-4 h-16 rounded bg-white shadow flex items-center gap-2 justify-between">
      <div className="flex items-center gap-1 sm:gap-2">
        <img className="w-5 sm:w-8 mt-1" src={CheckShadow} alt="" />
        <span className="text-xs sm:text-base xl:text-xl font-semibold">
          Payment details
        </span>
      </div>

      <div className="items-center gap-1 sm:gap-2 flex">
        <div className="w-1.5 h-0.5 bg-gray-700 rounded-full" />
        <div className="w-3 h-0.5 bg-gray-700 rounded-full hidden sm:block" />
        <div className="w-2 sm:w-3 h-0.5 bg-gray-700 rounded-full" />
        <div className="w-1.5 h-0.5 bg-gray-700 rounded-full" />
      </div>

      <div className="flex items-center gap-1 sm:gap-2">
        <img className="w-4 sm:w-6" src={CheckOutline} alt="" />
        <span className="text-xs sm:text-base xl:text-xl font-semibold text-[#777]">
          Complete Order
        </span>
      </div>
    </div>
  );
}
