import React from "react";
import { Link } from "react-router-dom";

const HeaderWithPhoto = ({ show_others }) => {
  return (
    <header className="h-header_height border-b bg-white relative z-50">
      <div className="x_container h-full flex items-center justify-between">
        <h4 className="font-semibold text-[#844FBA] text-lg">
          <Link to="/">muleland</Link>
        </h4>
        <ul className="cursor-pointer flex items-center gap-5">
          {show_others && (
            <>
              <li className="text-base xl:text-xl">
                <Link to="/status">Screen Status</Link>
              </li>
              <li className="text-base xl:text-xl">
                <Link to="/inbox">Messages</Link>
              </li>
            </>
          )}
          <li>
            <img
              className="w-[53px] h-[53px] rounded-full"
              src="https://randomuser.me/api/portraits/men/36.jpg"
              alt=""
            />
          </li>
        </ul>
      </div>
    </header>
  );
};

export default HeaderWithPhoto;
