import React from "react";
import { Link } from "react-router-dom";

const Home = () => {
  return (
    <div>
      <div
        style={{
          backgroundImage: `linear-gradient(to right, #00000070, #00000070), url(https://cdn.pixabay.com/photo/2018/07/24/19/08/vr-3559837_960_720.jpg)`,
        }}
        className="w-full h-screen overflow-y-auto bg-cover bg-center bg-black bg-fixed pb-6"
      >
        <header className="w-full h-[90px]">
          <div className="x_container h-full flex items-center justify-end">
            <Link to="/login">
              <button className="w-[146px] h-[54px] bg-white rounded-full focus:ring">
                For Experts
              </button>
            </Link>
          </div>
        </header>

        <div className="x_container flex flex-col pt-6 lg:pt-12 gap-20 px-6">
          <div>
            <h2 className="text-3xl lg:text-6xl font-bold text-white tracking-tight text-center">
              We are <br /> Coming Soon
            </h2>
            <p className="text-gray-300 text-base w-full md:w-9/12 mx-auto mt-5 text-center">
              Get ready we have something coming that is going to blow you away.
              Below is a sneak peek of what we offer. Check back often for our
              branch.
            </p>
          </div>
          <h2 className="text-3xl lg:text-6xl font-semibold tracking-wider text-center text-white">
            Muneland
          </h2>
        </div>
      </div>
    </div>
  );
};

export default Home;
