import React, { useEffect, useState } from "react";

const Star = () => (
  <svg
    className="w-4 min-w-[16px]"
    viewBox="0 0 22 21"
    fill="currentColor"
    xmlns="http://www.w3.org/2000/svg"
  >
    <path
      d="M9.71395 0.978319C10.0133 0.0570083 11.3167 0.0570102 11.6161 0.978321L13.4331 6.57045C13.5669 6.98247 13.9509 7.26143 14.3841 7.26143H20.264C21.2327 7.26143 21.6355 8.50105 20.8518 9.07045L16.0949 12.5266C15.7444 12.7812 15.5977 13.2326 15.7316 13.6446L17.5486 19.2367C17.8479 20.158 16.7935 20.9242 16.0097 20.3548L11.2528 16.8986C10.9023 16.644 10.4277 16.644 10.0772 16.8986L5.32027 20.3548C4.53656 20.9242 3.48208 20.158 3.78143 19.2367L5.59842 13.6446C5.7323 13.2326 5.58564 12.7812 5.23515 12.5266L0.478207 9.07045C-0.305507 8.50105 0.0972691 7.26143 1.06599 7.26143H6.9459C7.37913 7.26143 7.76309 6.98247 7.89696 6.57045L9.71395 0.978319Z"
      fill="currentColor"
    />
  </svg>
);
const Reviews = ({ rating, max_limit }) => {
  const [make_rating, set_make_rating] = useState(0);

  useEffect(() => {
    set_make_rating((100 / max_limit) * rating);
  }, [rating, max_limit]);

  return (
    <div className="flex w-fit gap-1 text-gray-300 relative">
      <Star />
      <Star />
      <Star />
      <Star />
      <Star />

      <div
        style={{
          width: `${make_rating}%`,
        }}
        className="flex gap-1 text-yellow-500 absolute inset-y-0 left-0 m-auto overflow-hidden"
      >
        <Star />
        <Star />
        <Star />
        <Star />
        <Star />
      </div>
    </div>
  );
};

export default Reviews;
