import React from "react";

const InputGroup = ({
  label,
  type = "text",
  required = true,
  name,
  id,
  ...props
}) => {
  const [focus, set_focus] = React.useState(false);
  const input_ref = React.useRef();
  return (
    <div className="relative pt-3">
      <label
        className={`absolute transition-all ${
          focus || input_ref.current?.value
            ? "top-0 text-xs font-medium text-[#999]"
            : "top-4 text-base text-[#777] "
        }`}
        htmlFor={id || label}
      >
        {label}
      </label>
      <input
        {...props}
        ref={input_ref}
        onFocus={() => set_focus(true)}
        onBlur={() => set_focus(false)}
        type={type}
        name={name || label}
        id={id || label}
        required={required}
        className="bg-transparent w-full focus:outline-none border-b border-[#777777] h-10 transition-none"
      />
    </div>
  );
};

export default InputGroup;
