import React, { useState } from "react";
import checkSuccess from "../../../assets/svgs/check-opacity.svg";
import exclamation from "../../../assets/svgs/exclamation.svg";

const InputWithIcon = ({
  type = "text",
  label,
  id,
  name,
  onChange,
  placeholder,
  Icon,
  required,
  minLength,
  ...props
}) => {
  const [touch, set_touch] = useState(false);
  const [ok, set_ok] = useState(false);

  function change_handle(e) {
    const value = e.target.value;

    if (value.length > 0) set_touch(true);

    if (type === "email") {
      if (
        value.length >= (minLength || 3) &&
        value.includes("@") &&
        value.includes(".")
      ) {
        set_ok(true);
      } else {
        set_ok(false);
      }
    } else {
      if (value.length >= (minLength || 1)) {
        set_ok(true);
      } else {
        set_ok(false);
      }
    }

    if (onChange) {
      onChange(e);
    }
  }

  return (
    <div>
      <label
        className="text-base font-semibold mb-1 block"
        htmlFor={id || name || label || ""}
      >
        {label}
      </label>
      <div className="relative">
        <img src={Icon} className="absolute inset-y-0 my-auto left-3" alt="" />
        <input
          onChange={change_handle}
          {...props}
          className="pl-12 pr-6 w-full h-[55px] border bg-white border-[#E0E0E0] rounded focus:outline-none focus:ring-2 transition-none focus:ring-[#77777750]"
          placeholder={placeholder}
          id={id || name || label || ""}
          name={name || id || label || ""}
          type={type}
          required={required || true}
        />

        {touch && (
          <img
            src={ok ? checkSuccess : exclamation}
            className="absolute inset-y-0 my-auto right-3"
            alt=""
          />
        )}
      </div>
    </div>
  );
};

export default InputWithIcon;
