import React from "react";

export const Card = ({ children, className, onClick, ...props }) => (
  <div
    {...props}
    onClick={onClick}
    className={`col-span-6 md:col-span-4 xl:col-span-3 p-3 flex flex-col justify-between rounded bg-[#F2F2F7] ${className}`}
  >
    {children}
  </div>
);

const CardGrids = ({ children }) => {
  return <div className="grid grid-cols-12 gap-3 sm:gap-9">{children}</div>;
};

export default CardGrids;
