import React, { useState } from "react";
import DarkButtonCircle from "../../components/Utilities/DarkButtonCircle";
import EyeIcon from "../../../assets/svgs/eye.svg";
import TimesBlack from "../../../assets/svgs/times-black.svg";
import OverlayPopup from "../../components/Utilities/OverlayPopup";

import { strategy_dision_data } from "./StrategyVision";
import { solution_playbook_data } from "./EntSolutionPlaybook";
import { leadership_socialization_data } from "./LeadershipSocialization";
import CardGrids, { Card } from "../../components/Categories/CardGrids";
import PlusIconBtn from "../../components/Utilities/PlusIconBtn";
import { Link } from "react-router-dom";

const SummaryBox = ({ title, cards, image, ...props }) => (
  <div
    {...props}
    className="col-span-12 sm:col-span-6 xl:col-span-4 2xl:col-span-3 p-3 flex flex-col justify-between rounded bg-[#F2F2F7] cursor-pointer"
  >
    <div className="w-full h-36 bg-white rounded flex items-center justify-center">
      <img className="w-20" src={image} alt="" />
    </div>
    <h3 className="text-[#222] text-xl font-semibold pt-5 h-24 leading-6 flex items-center">
      {title}
    </h3>
    <p className="text-sm font-semibold flex items-center gap-2">
      <span>{cards} Cards</span>
      <img src={EyeIcon} alt="" />
    </p>
  </div>
);

const Summary = () => {
  const [show_popup, set_show_popup] = useState(false);

  const [popup_data, set_popup_date] = useState(strategy_dision_data);
  const [popup_title, set_popup_title] = useState("");

  return (
    <>
      <h3 className="text-2xl font-semibold">Summary</h3>
      <br />
      <div className="bg-white p-10 rounded space-y-10">
        <p className="text-lg text-black leading-6">
          Here is the overall summary of what you can bring to your customers on
          their innovative projects. Please review your global scope again
          before publish it.
        </p>

        <div className="grid grid-cols-12 gap-3 sm:gap-9">
          <SummaryBox
            onClick={() => {
              set_show_popup(true);
              set_popup_title("Strategy & Vision");
              set_popup_date(strategy_dision_data);
            }}
            title="Strategy & Vision"
            image="https://i.ibb.co/WyyRMn2/chess.png"
            cards="4"
          />
          <SummaryBox
            onClick={() => {
              set_show_popup(true);
              set_popup_title("Ent Solution Playbook");
              set_popup_date(solution_playbook_data);
            }}
            title="Ent. Solution Playbook"
            image="https://i.ibb.co/Zx0kGgV/lightbulb-2.png"
            cards="4"
          />
          <SummaryBox
            onClick={() => {
              set_show_popup(true);
              set_popup_title("Leadership Socialization");
              set_popup_date(leadership_socialization_data);
            }}
            title="Leadership & Socialization"
            image="https://i.ibb.co/8b01zcT/united.png"
            cards="4"
          />
        </div>

        <Link to="/publish" className="block w-52">
          <DarkButtonCircle style={{ fontSize: "16px" }}>
            Publish
          </DarkButtonCircle>
        </Link>
      </div>

      <OverlayPopup show_popup={show_popup} set_show_popup={set_show_popup}>
        <div className="w-full lg:w-[900px] bg-white rounded relative z-50">
          <div className="relative border-b min-h-[75px] px-3 lg:px-10 flex items-center justify-between">
            <p className="text-xl w-11/12 text-left">
              Here are the cards you completed on the
              <span className="font-semibold ml-2">{popup_title}</span>
            </p>
            <button
              className="absolute inset-y-0 my-0 right-6"
              onClick={() => set_show_popup(false)}
            >
              <img src={TimesBlack} alt="" />
            </button>
          </div>

          <div className="px-3 lg:px-10 py-5">
            <CardGrids>
              {popup_data.map((d, i) => (
                <Card key={i}>
                  <div className="relative rounded overflow-hidden h-full">
                    <img className="w-full" src={d.image} alt="" />
                    <div className="absolute top-2 right-2">
                      <PlusIconBtn />
                    </div>
                  </div>
                  <p className="text-[#222] text-sm font-semibold pt-5 h-16 flex items-center">
                    {d.text}
                  </p>
                </Card>
              ))}
            </CardGrids>
          </div>
        </div>
      </OverlayPopup>
    </>
  );
};

export default Summary;
