import React from "react";
import ThreeDot from "../../../assets/svgs/three-dot2.svg";
import ArrowLeft from "../../../assets/svgs/arrow-left.svg";

const ChatMessagesHeader = ({
  name,
  chat_header_title,
  time,
  toggle_users,
}) => {
  return (
    <header className="flex items-center h-16 px-6 justify-start gap-3 md:gap-6 border-b">
      <button
        onClick={toggle_users}
        className="lg:hidden bg-gray-100 shadow border w-7 h-7 flex items-center justify-center rounded-full"
      >
        <img className="w-4" src={ArrowLeft} alt="" />
      </button>
      <h2 className="text-lg xl:text-2xl font-semibold">{name}</h2>
      <p className="text-xs xl:text-base text-[#777]">{chat_header_title}</p>
      <p className="text-xs xl:text-base text-[#777]">{time}</p>
      <button className="flex items-center justify-center w-7 h-7 rounded-full border border-transparent hover:border-gray-300 focus:border-transparent focus:ring ml-auto">
        <img className="w-8/12 object-contain" src={ThreeDot} alt="" />
      </button>
    </header>
  );
};

export default ChatMessagesHeader;
