import React from "react";
import HeaderWithPhoto from "../components/Headers/HeaderWithPhoto";
import DarkButtonCircle from "../components/Utilities/DarkButtonCircle";
import { Link } from "react-router-dom";

const PaymentSuccess = () => {
  return (
    <>
      <HeaderWithPhoto />
      <div
        style={{ minHeight: "calc(100vh - 75px)" }}
        className="x_container flex items-center justify-center py-12"
      >
        <div className="bg-[#F8F8F9] text-[#222] flex flex-col gap-9  w-full sm:w-[500px] p-10">
          <img
            className="w-44 h-44 mx-auto"
            src="https://i.ibb.co/jkR3xxj/success.png"
            alt=""
          />
          <h2 className="text-4xl font-semibold text-center">Great</h2>
          <div>
            <p className="text-xl text-center mb-4 leading-7">
              You have made payment for the Metaverse Measurement/KPI Strategy
            </p>
            <Link to="/freelancer-home">
              <DarkButtonCircle>Home</DarkButtonCircle>
            </Link>
          </div>
        </div>
      </div>
    </>
  );
};

export default PaymentSuccess;
