import React from "react";

const DarkButtonCircleOutline = ({
  children,
  onClick,
  type = "button",
  className,
  ...props
}) => {
  return (
    <button
      {...props}
      onClick={onClick}
      type={type}
      className={`border-[1.7px] border-[#222222] text-[#222222] w-full h-[55px] flex items-center justify-center text-center rounded-full text-xl font-semibold focus:ring focus:border-transparent ${className}`}
    >
      {children}
    </button>
  );
};

export default DarkButtonCircleOutline;
