const initState = {
  total_questions: 0,
  total_done: 0,
  total_correct: 0,
  total_wrong: 0,
  correct_percentage: 0,
};

const user = (state = initState, action) => {
  const { type, payload } = action;

  switch (type) {
    case "set_total_questions":
      return { ...state, total_questions: payload };

    case "increment_total_done":
      return { ...state, total_done: state.total_done + 1 };

    case "increment_total_correct":
      return { ...state, total_correct: state.total_correct + 1 };

    case "increment_total_wrong":
      return { ...state, total_wrong: state.total_wrong + 1 };

    case "set_correct_percentage":
      return { ...state, correct_percentage: payload };

    case "reset_quiz": {
      return {
        total_questions: 0,
        total_done: 0,
        total_correct: 0,
        total_wrong: 0,
        correct_percentage: 0,
      };
    }

    default:
      return state;
  }
};

export default user;
