import React from "react";
import ChatSingleUser from "./ChatSingleUser";
import ChatUsersSearch from "./ChatUsersSearch";

const ChatUsers = ({ show_users, toggle_users }) => {
  return (
    <div
      className={`w-9/12 sm:w-[500px] bg-white z-50 h-full pl-6 border-r absolute top-0 left-0 lg:static transform  ${
        show_users ? "translate-x-0" : "-translate-x-full lg:translate-x-0"
      }`}
    >
      {/* Search Chat */}
      <ChatUsersSearch />
      {/* Search Chat */}

      {/* Chat Users */}
      <div
        style={{ height: "calc(100% - 100px)" }}
        className="flex flex-col overflow-y-auto"
      >
        <ChatSingleUser
          name="Leslie Jones"
          date="5/27/22"
          chatText="Can you help with a project on"
          image="https://randomuser.me/api/portraits/men/1.jpg"
          toggle_users={toggle_users}
        />
        <ChatSingleUser
          selected={true}
          name="Wade Warren"
          date="5/27/22"
          chatText="Hello, can you help with a project on"
          image="https://randomuser.me/api/portraits/men/2.jpg"
          toggle_users={toggle_users}
        />
        <ChatSingleUser
          name="Devon Lane"
          date="5/27/22"
          chatText="Hi, can you help with a project on"
          image="https://randomuser.me/api/portraits/men/3.jpg"
          toggle_users={toggle_users}
        />
        <ChatSingleUser
          name="Eleanor Pena"
          date="5/27/22"
          chatText="Hey Eleanor, can you help with a project on"
          image="https://randomuser.me/api/portraits/women/19.jpg"
          toggle_users={toggle_users}
        />
        <ChatSingleUser
          name="Ralph Edwards"
          date="5/27/22"
          chatText="Hello, can you help with a project on"
          image="https://randomuser.me/api/portraits/men/20.jpg"
          toggle_users={toggle_users}
        />
        <ChatSingleUser
          name="Kathryn Murphy"
          date="5/27/22"
          chatText="Hi, can you help with a project on"
          image="https://randomuser.me/api/portraits/women/20.jpg"
          toggle_users={toggle_users}
        />
        <ChatSingleUser
          name="Jacob Jones"
          date="5/27/22"
          chatText="Hello, can you help with a project on"
          image="https://randomuser.me/api/portraits/women/21.jpg"
          toggle_users={toggle_users}
        />
      </div>
      {/* Chat Users */}
    </div>
  );
};

export default ChatUsers;
