import React, { useState } from "react";
import HeaderWithPhoto from "../components/Headers/HeaderWithPhoto";
import DarkButtonCircle from "../components/Utilities/DarkButtonCircle";

import { Navigate } from "react-router-dom";

import { useDispatch } from "react-redux";

const TryAgain = () => {
  const dispatch = useDispatch();
  const [go_questions, set_gq] = useState(false);

  function tryAgain() {
    dispatch({ type: "reset_quiz" });
    set_gq(true);
  }

  if (go_questions) {
    return <Navigate to="/quiz-questions" />;
  }

  return (
    <>
      <HeaderWithPhoto />
      <div
        style={{ minHeight: "calc(100vh - 75px)" }}
        className="x_container flex items-center justify-center py-12"
      >
        <div className="bg-[#F8F8F9] text-[#222] flex flex-col gap-9  w-full sm:w-[500px] p-10">
          <img
            className="w-44 h-44 mx-auto"
            src="https://i.ibb.co/4pSSVx6/negative-vote.png"
            alt=""
          />
          <h2 className="text-4xl text-center font-semibold">Opps..</h2>
          <p className="text-xl text-center">
            You failed the quiz, please wait 24 hours before taking the quiz
            again
          </p>
          <DarkButtonCircle onClick={tryAgain}>Try again</DarkButtonCircle>
        </div>
      </div>
    </>
  );
};

export default TryAgain;
