import React from "react";
import HeaderWithPhoto from "../components/Headers/HeaderWithPhoto";

const FreelancerProfileStatus = () => {
  return (
    <>
      <HeaderWithPhoto show_others={true} />

      <div
        style={{ minHeight: "calc(100vh - 75px)" }}
        className="w-full bg-[#F2F2F7] text-[#222] py-8 2xl:py-10"
      >
        <div className="x_container">
          <h3 className="text-2xl 2xl:text-3xl mb-5">Screen Status</h3>

          {/* Table */}
          <div className="overflow-x-auto relative shadow-md rounded">
            <table className="w-full text-sm text-left">
              <thead className="text-xs text-[#777] uppercase bg-gray-50 h-[14]">
                <tr>
                  <th scope="col" className="text-xl py-3 px-6">
                    FREELANCER
                  </th>
                  <th scope="col" className="text-xl py-3 px-6">
                    PURCHASE
                  </th>
                  <th scope="col" className="text-xl py-3 px-6">
                    STATUS
                  </th>
                  <th scope="col" className="text-xl py-3 px-6">
                    DUE DATE
                  </th>
                  <th scope="col" className="text-xl py-3 px-6">
                    RATE
                  </th>
                  <th scope="col" className="py-3 px-6 text-transparent">
                    ACTION
                  </th>
                </tr>
              </thead>
              <tbody>
                <TableBox
                  name="Leslie Alexander"
                  category="NFT & Blockchain"
                  image="https://randomuser.me/api/portraits/men/11.jpg"
                  status_text="in progress"
                  due_date="JUL 16"
                  rate="120"
                />
                <TableBox
                  name="John D."
                  category="NFT & Blockchain"
                  image="https://randomuser.me/api/portraits/men/10.jpg"
                  status_text="soon ready"
                  due_date="JUL 16"
                  rate="120"
                />
                <TableBox
                  name="Sagar Roy"
                  category="NFT & Blockchain"
                  image="https://randomuser.me/api/portraits/men/12.jpg"
                  status_text="ready"
                  due_date="JUL 16"
                  rate="120"
                />
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </>
  );
};

export default FreelancerProfileStatus;

function TableBox({ name, category, image, status_text, due_date, rate }) {
  return (
    <tr className="bg-white border-b">
      <td className="py-4 px-6">
        <div className="flex items-center gap-2">
          <img className="w-14 h-14 rounded-full" src={image} alt="" />
          <span className="text-lg font-semibold">{name}</span>
        </div>
      </td>

      <td className="py-4 px-6 text-lg">{category}</td>
      <td className="py-4 px-6 text-lg">
        <StatusText />
      </td>
      <td className="py-4 px-6 text-lg">{due_date}</td>
      <td className="py-4 px-6 text-lg">${rate}</td>
      <td className="py-4 px-6">
        <button className="px-2 py-1 rounded border focus:border-transparent focus:ring">
          View
        </button>
      </td>
    </tr>
  );

  function StatusText() {
    if (status_text?.trim()?.toLowerCase() === "ready") {
      return (
        <span className="min-w-fit flex items-center justify-center text-[#27AE60] bg-[#27AE6040] rounded px-4 py-1">
          {status_text}
        </span>
      );
    } else if (status_text?.trim()?.toLowerCase() === "soon ready") {
      return (
        <span className="min-w-fit flex items-center justify-center text-[#D2B758] bg-[#D2B75840] rounded px-4 py-1">
          {status_text}
        </span>
      );
    } else {
      return (
        <span className="min-w-fit flex items-center justify-center text-[#844FBA] bg-[#844FBA40] rounded px-4 py-1">
          {status_text}
        </span>
      );
    }
  }
}
