import React, { useEffect, useRef, useState } from "react";

export default function DragableWidthChange({
  width,
  get_percentage,
  dragable_bg,
  circle_bg,
}) {
  const dragable_parent = useRef();
  const dragable = useRef();

  const [size, setSize] = useState({ x: Math.round(Math.random() * 100) });

  useEffect(() => {
    if (get_percentage) {
      get_percentage(
        Math.round(
          (100 * dragable.current?.clientWidth) /
            dragable_parent.current?.clientWidth
        )
      );
    }
  }, [size, get_percentage]);

  const handler = (mouseDownEvent) => {
    const startSize = size;
    const startPosition = { x: mouseDownEvent.pageX, y: mouseDownEvent.pageY };

    function onMouseMove(mouseMoveEvent) {
      setSize(() => ({
        x: startSize.x - startPosition.x + mouseMoveEvent.pageX,
      }));
    }

    function onMouseUp() {
      dragable_parent.current?.removeEventListener("mousemove", onMouseMove);
    }

    dragable_parent.current?.addEventListener("mousemove", onMouseMove);
    dragable_parent.current?.addEventListener("mouseup", onMouseUp, {
      once: true,
    });
  };

  return (
    <div
      ref={dragable_parent}
      className={`${width ? width : "w-44"} h-2 bg-gray-400 rounded-xl`}
    >
      <div
        ref={dragable}
        className={`${
          dragable_bg ? dragable_bg : "bg-black"
        } relative h-2 rounded-xl`}
        style={{ width: size.x, minWidth: "10%", maxWidth: "100%" }}
      >
        <button
          className={`${
            circle_bg ? circle_bg : "bg-[#4F4F4F]"
          } absolute right-0 inset-y-0 my-auto w-4 h-4 rounded-full`}
          onMouseDown={handler}
        ></button>
      </div>
    </div>
  );
}
