import React from "react";

const DarkButton = ({
  children,
  onClick,
  type = "button",
  className,
  ...props
}) => {
  return (
    <button
      {...props}
      onClick={onClick}
      type={type}
      className={`bg-[#222222] text-white w-full h-[48px] flex items-center justify-center text-center rounded text-base font-semibold focus:ring ${className}`}
    >
      {children}
    </button>
  );
};

export default DarkButton;
