import React from "react";
import { useSelector, useDispatch } from "react-redux";

const PaymentMethodImage = ({ image, method, onClick, ...props }) => {
  const dispatch = useDispatch();
  const { selected_method } = useSelector((state) => state?.payment);

  function clickHandle() {
    dispatch({ type: "set_method", payload: method });
    if (onClick) {
      onClick();
    }
  }

  return (
    <button
      onClick={clickHandle}
      {...props}
      className={`flex items-center justify-center bg-white lg:bg-[#F2F2F7] shadow rounded w-[310px] h-[90px] border ${
        selected_method?.toLowerCase() === method?.toLowerCase()
          ? "border-blue-500"
          : "border-transparent"
      }`}
    >
      <img className="w-12" src={image} alt="" />
    </button>
  );
};

export default PaymentMethodImage;
