import React, { useState } from "react";

import ArrowRight from "../../assets/svgs/arrow-right.svg";
import FillBox from "../../assets/svgs/fill-box.svg";
import HeaderWithPhoto from "../components/Headers/HeaderWithPhoto";

import { Navigate } from "react-router-dom";

const Box = ({ image, title, description }) => {
  function navigate_handle() {
    return;
  }

  return (
    <div
      onClick={navigate_handle}
      className="w-[260px] bg-[#F8F8F9] rounded p-4 space-y-4"
    >
      <div className="w-full h-36 bg-white flex items-center justify-center rounded">
        <img src={image} alt="" />
      </div>
      <h3 className="text-2xl font-semibold">{title}</h3>
      <p className="pb-2 leading-5 text-[#777]">{description}</p>
    </div>
  );
};

const FillBoxes = () => {
  const [go_fill_boxes, set_gfb] = useState(false);

  if (go_fill_boxes) {
    return <Navigate to="/categories" />;
  }
  return (
    <>
      <HeaderWithPhoto />
      <div className="text-[#333]">
        <div className="x_container flex flex-col gap-6 py-12">
          <h2
            className="flex flex-col sm:flex-row items-start sm:items-center gap-5 font-semibold"
            style={{ fontSize: "42px" }}
          >
            <span> Munelands offerings : Fill your box</span>
            <img className="w-20" src={FillBox} alt="" />
          </h2>
          <p className="lg:w-[999px] text-xl leading-8">
            Through 3 workstreams, Visit each category, choose the cards that
            best suit your service and customize them to your liking. Take the
            time to read the global outputs of each card so that you cand
            deliver your best service on an Blockchain/Metaverse/NFT project
          </p>

          <div
            onClick={() => set_gfb(true)}
            className="flex flex-col lg:flex-row items-center gap-5 cursor-pointer"
          >
            <Box
              title="Strategy & Vision"
              description="Identify and define opportunities across the Enterprise and business units"
              image="https://i.ibb.co/WyyRMn2/chess.png"
            />
            <div className="transform rotate-90 py-5 lg:py-0 lg:rotate-0">
              <img src={ArrowRight} alt="" />
            </div>
            <Box
              title="Ent. Solution Playbook"
              description="Design the system to support and mobilize  activation"
              image="https://i.ibb.co/Zx0kGgV/lightbulb-2.png"
            />
            <div className="transform rotate-90 py-5 lg:py-0 lg:rotate-0">
              <img src={ArrowRight} alt="" />
            </div>
            <Box
              title="Leadership & Socialization"
              description="Gain program buy-in and momentum within the organization"
              image="https://i.ibb.co/8b01zcT/united.png"
            />
          </div>
        </div>
      </div>
    </>
  );
};

export default FillBoxes;
