import React, { useState } from "react";
import Mail from "../../../assets/svgs/mail.svg";
import Card from "../../../assets/svgs/credit-card.svg";
import User from "../../../assets/svgs/user-dark.svg";
import Flag from "../../../assets/svgs/flag.svg";
import InputWithIcon from "../Utilities/InputWithIcon";
import DarkButtonCircle from "../Utilities/DarkButtonCircle";
import { Navigate } from "react-router-dom";
import PaymentRightbarHeader from "./PaymentRightbarHeader";

const PaymentRightbar = () => {
  const [go_confirm, set_go_confirm] = useState(false);

  function pay_submit(e) {
    e.preventDefault();
    set_go_confirm(true);
  }

  if (go_confirm) {
    return <Navigate to="/confirm-payment" />;
  }

  return (
    <div className="w-full pt-6 pb-9 relative z-20">
      <form
        onSubmit={pay_submit}
        className="w-full sm:w-[90%] lg:w-[95%] xl:w-[90%] 2xl:w-[600px] mx-auto lg:mx-0 lg:ml-auto 2xl:mx-auto space-y-8"
      >
        <PaymentRight />

        {/* Payment Details Inputs */}
        <div className="space-y-5">
          <InputWithIcon
            onChange={(e) => {}}
            placeholder="exmaple@mail.com"
            type="email"
            Icon={Mail}
            label="Email Address"
          />
          <InputWithIcon
            onChange={(e) => {}}
            placeholder="Card Number"
            Icon={Card}
            label="Card Details"
            minLength={6}
          />
          <InputWithIcon
            onChange={(e) => {}}
            placeholder="Sagar Roy"
            Icon={User}
            label="Card Holder"
          />
          <div className="w-full">
            <InputWithIcon
              onChange={(e) => {}}
              placeholder="7581 Garfield Ave,Huntington Beach "
              Icon={Flag}
              label="Billing Address"
            />
            <div className="flex">
              <input
                type="text"
                placeholder="State"
                className="w-full h-[55px] bg-white border px-4 focus:outline-none focus:border-gray-400 transition-none text-center"
              />
              <input
                type="text"
                placeholder="Postal Code/ZIP"
                className="w-full h-[55px] bg-white border px-4 focus:outline-none focus:border-gray-400 transition-none text-center"
              />
            </div>
          </div>
        </div>

        {/* Amount Details Footer */}
        <div className="space-y-2">
          <div className="flex justify-between">
            <span className="text-lg">Subtotal</span>
            <span>$ 50</span>
          </div>
          <div className="flex justify-between">
            <span className="text-lg">Vat (20%)</span>
            <span>$ 10</span>
          </div>
          <div className="flex justify-between">
            <b className="text-lg">Total</b>
            <b>$ 50</b>
          </div>
        </div>

        {/* Pay Button */}
        <DarkButtonCircle type="submit">Pay $60</DarkButtonCircle>
      </form>
    </div>
  );
};

export default PaymentRightbar;

function PaymentRight() {
  return (
    <>
      <PaymentRightbarHeader />

      <div className="space-y-2">
        <h3 className="text-2xl font-semibold">Payment Details</h3>
        <p className="text-[#777] text-lg leading-5">
          Complete your purchase package by providing your payment details below
        </p>
      </div>
    </>
  );
}
