import React, { useEffect, useState } from "react";
import ChevronTop from "../../../assets/svgs/chevron-top.svg";
import DragableWidthChange from "../Utilities/DragableWidthChange";
import FilterInput from "../Utilities/Input";
import SidebarFilterItem from "./SidebarFilterItem";

const Sidebar = () => {
  const [show_for_small_device, set_show] = useState(false);
  const [rate_percentage, set_rate_percentage] = useState(0);

  // See on console rate price range percentage
  useEffect(() => {
    console.log("rate_percentage ", rate_percentage);
  }, [rate_percentage]);

  return (
    <nav
      className={`fixed top-header_height left-0 transform lg:relative lg:top-0 lg:translate-x-0 min-w-[340px] h-minus_header_screen bg-white transition-all ${
        show_for_small_device ? "translate-x-0 shadow" : "-translate-x-full"
      }`}
    >
      <div className="px-6 lg:px-10 space-y-8 py-12 w-full h-full overflow-y-auto">
        <h3 className="font-medium text-3xl mb-12">Filter</h3>

        <SidebarFilterItem title="Location" htmlFor="location_search">
          <FilterInput placeholder="Search location" id="location_search" />
        </SidebarFilterItem>

        <SidebarFilterItem title="Industry Network" htmlFor="industry_network">
          <FilterInput placeholder="Search Industry" id="industry_network" />
        </SidebarFilterItem>

        <SidebarFilterItem title="Languages" htmlFor="Search language">
          <FilterInput placeholder="Search Languages" id="Search language" />
        </SidebarFilterItem>

        <SidebarFilterItem title="Testimonials" htmlFor="testimonials">
          <FilterInput placeholder="Search testimonials" id="testimonials" />
        </SidebarFilterItem>

        <SidebarFilterItem
          title="Certifications"
          htmlFor="search_certification"
        >
          <FilterInput placeholder="Certifications" id="search_certification" />
        </SidebarFilterItem>

        <SidebarFilterItem title="Technologies" htmlFor="search_technologies">
          <FilterInput
            placeholder="Search technologies"
            id="search_technologies"
          />
        </SidebarFilterItem>

        <SidebarFilterItem title="Years of experience" htmlFor="years_of_exp">
          <FilterInput placeholder="Years of experience" id="years_of_exp" />
        </SidebarFilterItem>

        <SidebarFilterItem title="Rate">
          <DragableWidthChange
            get_percentage={set_rate_percentage}
            width="w-full"
            dragable_bg="bg-blue-600"
            circle_bg="bg-blue-600"
          />

          <div className="w-full flex justify-between mt-4">
            <span className="text-xl text-[#777]">$0</span>
            <span className="text-xl text-[#777]">$200</span>
            <span className="text-xl text-[#777]">$600</span>
          </div>
        </SidebarFilterItem>
      </div>

      {/* Toggler For Small Device */}
      <button
        onClick={() => set_show((p) => !p)}
        className="absolute top-6 lg:hidden -right-12 w-12 h-12 bg-white shadow flex items-center justify-center rounded-r-lg z-50 cursor-pointer focus:ring"
      >
        <img
          className={`w-7 h-7 transform transition-all ${
            show_for_small_device ? "-rotate-90" : "rotate-90"
          }`}
          src={ChevronTop}
          alt=""
        />
      </button>
      {/* Toggler For Small Device */}
    </nav>
  );
};

export default Sidebar;
