import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";

import CheckGreen from "../../../assets/svgs/check-green.svg";
import TimesOutline from "../../../assets/svgs/times-outline.svg";

const SingleQuestion = ({ quiz }) => {
  const dispatch = useDispatch();

  const [answer, set_answer] = useState("");
  const [btn_index, set_btn_index] = useState(null);

  useEffect(() => {
    if (answer) {
      dispatch({ type: "increment_total_done" });
      if (answer === quiz.answer) {
        dispatch({ type: "increment_total_correct" });
      } else {
        dispatch({ type: "increment_total_wrong" });
      }
    }
  }, [quiz, answer, dispatch]);

  function answer_handler(question, index) {
    if (!answer) {
      set_answer(question);
      dispatch({ type: "set_total_wrong", payload: 1 });

      set_btn_index(index);
    }
  }

  return (
    <>
      <div className="relative -mb-2">
        <h4 className="text-2xl font-semibold">{quiz?.name}</h4>
        <div className="w-full h-full absolute inset-0 m-auto bg-transparent z-30" />
      </div>

      <img
        className="w-full h-54 object-cover object-center"
        src={quiz?.image}
        alt=""
      />
      <div className="flex flex-col gap-6">
        {quiz?.questions?.map((question, i) => (
          <button
            disabled={answer}
            onClick={() => answer_handler(question, i)}
            key={i}
            className={`w-full h-[61px] text-2xl border-2 border-[#999] rounded-full capitalize hover:bg-gray-300  ${
              answer && i === btn_index
                ? `${
                    answer === quiz.answer
                      ? "bg-[#21965350] hover:bg-[#21965350]"
                      : "bg-[#ff424270] hover:bg-[#ff424270]"
                  }`
                : `disabled:cursor-not-allowed disabled:hover:bg-transparent`
            }`}
          >
            {question}
          </button>
        ))}
      </div>
      {answer && (
        <>
          {answer === quiz.answer ? (
            <div className="bg-[#21965350] p-6 rounded shadow">
              <p className="text-2xl font-semibold flex items-center gap-2">
                <img src={CheckGreen} alt="" />
                CORRECT
              </p>
              <p className="text-black mt-5">
                The term metaverse first appeared in author Neal Stephenson's
                1992 science-fiction novel Snow Crash, which depicts a virtual
                reality-based successor to the internet.
              </p>
            </div>
          ) : (
            <div className="bg-[#ff424270] p-6 rounded shadow">
              <p className="text-2xl font-semibold flex items-center gap-2">
                <img src={TimesOutline} alt="" />
                WRONG
              </p>
              <p className="text-black mt-5">
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Totam
                nulla qui beatae laboriosam asperiores dolore! Natus odio
                pariatur dolor alias magni.
              </p>
            </div>
          )}
        </>
      )}
      <div className="h-2" />
    </>
  );
};

export default SingleQuestion;
