import React, { useState } from "react";

import ChevronTop from "../../../assets/svgs/chevron-top.svg";

const SidebarFilterItem = ({ title, htmlFor, children }) => {
  const [open_child, set_open_child] = useState(false);

  return (
    <div>
      <label
        onClick={() => set_open_child((p) => !p)}
        htmlFor={htmlFor || ""}
        className="flex items-center justify-between cursor-pointer"
      >
        <span className="text-xl font-medium">{title}</span>

        <img
          className={`transform transition-all ${
            open_child ? "rotate-0" : "rotate-180"
          }`}
          src={ChevronTop}
          alt=""
        />
      </label>
      {open_child && <div className="my-5">{children}</div>}
    </div>
  );
};

export default SidebarFilterItem;
