import React from "react";

const SelectInput = ({
  id,
  name,
  className,
  onChange,
  value,
  placeholder,
  children,
  ...props
}) => {
  return (
    <div className="w-full relative">
      <select
        {...props}
        onChange={onChange}
        className={`w-full h-12 ring-1 ring-[#E0E0E0] focus:ring-blue-300 rounded focus:outline-none focus:ring border placeholder:text-[#777] border-gray-50 bg-gray-50 border-r-8 border-l-8 transition-none ${className}`}
        id={id || name || placeholder}
        value={value || ""}
        name={name || id}
      >
        <option className="text-gray-400" value="">
          {placeholder}
        </option>
        {children}
      </select>
    </div>
  );
};

export default SelectInput;
