import React, { useState } from "react";
import HeaderWithPhoto from "../components/Headers/HeaderWithPhoto";
import ChatUsers from "../components/Inbox/ChatUsers";
import ChatMessages from "../components/Inbox/ChatMessages";

const Inbox = () => {
  // For Small Device
  const [show_users, set_show_users] = useState(false);
  function toggle_users() {
    set_show_users((p) => !p);
  }

  return (
    <>
      <HeaderWithPhoto show_others={true} />

      <div
        style={{ minHeight: "calc(100vh - 75px)" }}
        className="w-full bg-[#F2F2F7] text-[#222] flex items-center"
      >
        <div className="x_container rounded shadow bg-white">
          <div
            style={{
              height: "calc(100vh - 110px)",
            }}
            className="flex flex-col lg:flex-row justify-between items-stretch overflow-hidden relative"
          >
            <ChatUsers show_users={show_users} toggle_users={toggle_users} />
            <ChatMessages toggle_users={toggle_users} />

            {/* Chat Users Overlay For Small Devices */}
            {show_users && (
              <div
                onClick={toggle_users}
                className="lg:hidden w-full h-full bg-black absolute inset-0 m-auto z-20 bg-opacity-40"
              />
            )}
          </div>
        </div>
      </div>
    </>
  );
};

export default Inbox;
