import React from "react";

const ChatSingleUser = ({
  name,
  image,
  date,
  chatText,
  selected,
  toggle_users,
}) => {
  return (
    <div
      // onClick Toggle Chat Users for Small Device
      onClick={toggle_users}
      className={`flex items-center gap-3 border-b py-5 pr-6 cursor-pointer ${
        selected ? "bg-gray-100" : "hover:bg-gray-50"
      }`}
    >
      <div>
        <a href="##" className="flex items-center w-[60px] h-[60px]">
          <img
            className="max-w-full max-h-full rounded-full"
            src={image}
            alt=""
          />
        </a>
      </div>

      <div className="w-full">
        <div className="w-full flex justify-between">
          <h4 className="text-xl font-semibold">{name}</h4>
          <p className="text-[#777] text-sm">{date}</p>
        </div>
        <p className="text-base text-[#777] w-8/12 truncate h-6">{chatText}</p>
      </div>
    </div>
  );
};

export default ChatSingleUser;
