import React from "react";
import ClockIcon from "../../../assets/svgs/clock.svg";
import DollerIcon from "../../../assets/svgs/doller-outline.svg";
import PlusIconBtn from "../Utilities/PlusIconBtn";
import { Link } from "react-router-dom";

const ServiceCard = ({
  image,
  title,
  price,
  delivery_days,
  onClick_plusIcon,
  ...props
}) => {
  return (
    <div
      className={`w-[210px] p-3 shadow space-y-4 rounded bg-[#F2F2F7] flex flex-col justify-between`}
      {...props}
    >
      <div>
        <div className="relative">
          <img className="w-full" src={image} alt="" />
          <div className="absolute top-3 right-3">
            <PlusIconBtn
              onClick={onClick_plusIcon ? onClick_plusIcon : () => {}}
            />
          </div>
        </div>
        <p className="font-semibold text-sm mt-3">{title}</p>
      </div>

      <div>
        <div className="flex items-center gap-2">
          <img className="w-5 h-5" src={ClockIcon} alt="" />
          <span className="font-semibold text-sm text-[#777]">
            {delivery_days} days Delivery
          </span>
        </div>
        <div className="flex items-center gap-2 mt-2 mb-4">
          <img className="w-5 h-5" src={DollerIcon} alt="" />
          <span className="font-semibold text-sm text-[#777]">${price}</span>
        </div>

        <Link to="/payment">
          <button
            className={`bg-[#222222] text-white w-full h-[40px] flex items-center justify-center text-center rounded-full text-sm font-semibold focus:ring`}
          >
            Continue
          </button>
        </Link>
      </div>
    </div>
  );
};

export default ServiceCard;
