import React from "react";
import ServiceCard from "../../components/Utilities/ServiceCard";
import TestimonialsReview from "../../components/Utilities/TestimonialsReview";

const LeadershipSocialization = () => {
  return (
    <div>
      <h3 className="px-6 text-2xl font-semibold mb-5">
        Here are the Activities and Outcomes that I can do for you
      </h3>

      {/* Services Box */}
      <div className="px-6 flex flex-wrap justify-center xl:justify-start gap-6">
        <ServiceCard
          title="1. Enterprise Stakeholder Socialization Strategy"
          image="https://i.ibb.co/K2pYwQJ/Leadership-Socialization-4.png"
          price="50"
          delivery_days="15"
        />
        <ServiceCard
          title="2. Board Meeting Presentation"
          image="https://i.ibb.co/r43LFnY/Leadership-Socialization-3.png"
          price="20"
          delivery_days="11"
        />
        <ServiceCard
          title="3. Internal Communications Plan & Execution"
          image="https://i.ibb.co/w61gRGD/Leadership-Socialization-2.png"
          price="150"
          delivery_days="5"
        />
        <ServiceCard
          title="4. Partnership Term Sheet"
          image="https://i.ibb.co/WvSQbzW/Leadership-Socialization-1.png"
          price="125"
          delivery_days="4"
        />
      </div>
      {/* Services Box */}

      {/* Testimonials / Reviews */}
      <div className="pt-7 mt-12 border-t">
        <h3 className="px-6 text-2xl font-semibold">Testimonials</h3>

        <div className="px-6 mt-3">
          <TestimonialsReview
            rating={2.5}
            rating_max_limit={5}
            title="Sammy L."
            date="July 6th 2022"
            description="The end product of the project was exactly how i envisioned it. Leslie      used his experience combined with the design specification to deliver an excellent result. Thank you"
          />
          <TestimonialsReview
            rating={70}
            rating_max_limit={100}
            title="Sagar Roy"
            date="July 6th 2022"
            description="The end product of the project was exactly how i envisioned it.  Leslie used his experience combined with the design specification to deliver an excellent result. Thank you"
          />
          <TestimonialsReview
            rating={3}
            rating_max_limit={10}
            title="Johnson M."
            date="July 6th 2022"
            description="The end product of the project was exactly how i envisioned it. Leslie      used his experience combined with the design specification to deliver an excellent result. Thank you"
          />
        </div>
      </div>
      {/* Testimonials / Reviews */}
    </div>
  );
};

export default LeadershipSocialization;
