import React, { useEffect, useState } from "react";
import InputGroup from "../components/Forms/InputGroup";
import { useNavigate } from "react-router-dom";

import GoogleIcon from "../../assets/svgs/google.svg";
import DarkButton from "../components/Utilities/DarkButton";
import OutlineButton from "../components/Utilities/OutlineButton";

const Login = () => {
  const navigate = useNavigate();
  const [inputs_value, set_inputs_value] = useState({});

  function handle_input(e) {
    set_inputs_value((p) => ({
      ...p,
      [e.target.name]: e.target.value,
    }));
  }

  useEffect(() => {
    console.log("input values ", inputs_value);
  }, [inputs_value]);

  function register_handle(e) {
    e.preventDefault();

    navigate("/rate-skill");
  }

  return (
    <div className="w-full min-h-screen bg-white">
      <header className="h-header_height border-b">
        <div className="x_container h-full flex items-center justify-start">
          <h4 className="font-semibold text-[#844FBA] text-lg">muleland</h4>
        </div>
      </header>

      <div className="w-full relative">
        <div
          style={{ minHeight: "calc(100vh - 75px)" }}
          className="x_container flex items-center"
        >
          {/* Register Form */}
          <div className="w-full lg:w-6/12 h-full flex items-center justify-center lg:justify-start py-10 overflow-y-auto">
            <form
              onSubmit={register_handle}
              className="flex flex-col gap-5 w-full sm:w-[350px] px-1"
            >
              <h3 className="font-semibold text-2xl">
                Create an Expert Account
              </h3>
              <InputGroup
                onChange={handle_input}
                name="name"
                label="Full Name"
              />
              <InputGroup
                onChange={handle_input}
                name="telephone"
                label="Telephone Number"
              />
              <InputGroup
                onChange={handle_input}
                name="email"
                label="Email Address"
                type="email"
              />
              <InputGroup
                onChange={handle_input}
                name="nickname"
                label="Nickname"
              />

              {/* Register Now button */}
              <DarkButton type="submit" className="mt-5">
                Register Now
              </DarkButton>

              {/* Signup with google button */}
              <OutlineButton>
                <img src={GoogleIcon} alt="" />
                <span className="text-sm font-semibold text-[#222222]">
                  Sign up with Google
                </span>
              </OutlineButton>
            </form>
          </div>
          {/* Register Form */}
        </div>

        {/* Login Screen Background Image */}
        <div
          style={{
            backgroundImage: "url(https://i.ibb.co/QbXfrCC/login-screen.png)",
          }}
          className="w-6/12 hidden lg:block absolute top-0 right-0 h-minus_header_screen bg-no-repeat bg-center bg-cover"
        ></div>
        {/* Login Screen Background Image */}
      </div>
    </div>
  );
};

export default Login;
