import React, { useEffect, useRef, useState } from "react";
import ArrowLeft from "../../../assets/svgs/arrow-left.svg";

const BottomToTop = () => {
  const btn = useRef();
  const [show,set_show]= useState(false)
  useEffect(() => {
    window.addEventListener("scroll", () => {
      const scrollTop = document.documentElement.scrollTop;
      if (scrollTop > 1000) {
        set_show(true)
      } else {
        set_show(false)
      }
    });
  }, [btn]);

  function scrollTop() {
    document.documentElement.scrollTop = 0;
  }

  return (
    <button
      onClick={scrollTop}
      ref={btn}
      className={`${show?'flex':'hidden'} w-14 h-14 rounded items-center justify-center bg-green-500 bg-opacity-50 opacity-70 hover:opacity-100 fixed z-40 right-12 bottom-12`}
    >
      <img className="transform rotate-90" src={ArrowLeft} alt="" />
    </button>
  );
};

export default BottomToTop;
