import React, { useState } from "react";
import SearchIcon from "../../../assets/svgs/search-icon.svg";
import FilterIcon from "../../../assets/svgs/filter.svg";
import TimesOutline from "../../../assets/svgs/times-outline.svg";
import { Link } from "react-router-dom";

const Header = () => {
  const [show_search_small_device, set_show_search] = useState(false);

  return (
    <header className="h-header_height border-b bg-white relative z-50">
      <div className="x_container h-full flex items-center justify-between">
        <h4 className="font-semibold text-[#844FBA] text-lg">
          <Link to="/">muleland</Link>
        </h4>

        {/* Header Search */}
        <div
          className={`transform origin-top ${
            show_search_small_device
              ? "scale-y-100"
              : "scale-y-0 md:scale-y-100"
          } fixed md:relative z-50 top-[80px] right-6 md:top-0 md:right-0`}
        >
          <label
            htmlFor="search"
            className="absolute z-50 flex items-center inset-y-0 my-auto left-4"
          >
            <img className="w-5" src={SearchIcon} alt="" />
          </label>
          <input
            className="w-80 lg:w-96 h-12 rounded-full px-12 bg-[#f2f2f2] focus:outline-none focus:ring-2"
            placeholder="Search..."
            type="text"
            id="search"
          />
          <button className="absolute z-50 inset-y-0 my-auto right-6">
            <img className="w-5" src={FilterIcon} alt="" />
          </button>
        </div>
        {/* Header Search */}

        <ul className="flex items-center gap-4 md:gap-8 text-[#222]">
          <li onClick={() => set_show_search((p) => !p)} className="md:hidden">
            {show_search_small_device ? (
              <button className="flex items-center">
                <img src={TimesOutline} alt="" />
              </button>
            ) : (
              <label
                className="text-xs sm:text-lg cursor-pointer"
                htmlFor="search"
              >
                <img src={SearchIcon} alt="" />
              </label>
            )}
          </li>
          <li className="text-base xl:text-xl">
            <Link to="/status">Screen Status</Link>
          </li>
          <li className="text-xs sm:text-lg">
            <Link to="/inbox">Messages</Link>
          </li>
          <li className="cursor-pointer">
            <img
              className="w-[53px] h-[53px] rounded-full"
              src="https://randomuser.me/api/portraits/men/36.jpg"
              alt=""
            />
          </li>
        </ul>
      </div>
    </header>
  );
};

export default Header;
