import React from "react";
import HeaderWithPhoto from "../components/Headers/HeaderWithPhoto";
import PaymentLeftbar from "../components/Payment/PaymentLeftbar";
import PaymentRightbar from "../components/Payment/PaymentRightbar";

const Payment = () => {
  return (
    <>
      <HeaderWithPhoto />
      <div className="w-full bg-[#F2F2F7] text-[#222] min-h-screen relative adjust_bg">
        <div className="x_container  flex flex-col lg:flex-row lg:items-stretch">
          {/* Left Sidebar */}
          <PaymentLeftbar />

          {/* Right Sidebar */}
          <PaymentRightbar />
        </div>
      </div>
    </>
  );
};

export default Payment;
