import React from "react";

import ServiceCard from "../FreelancerProfile/ServiceCard";
import BackLinkButton from "../Utilities/BackLinkButton";
import PaymentMethodImage from "../Utilities/PaymentMethodImage";

const PaymentLeftbar = () => {
  return (
    <div className="w-full py-6 relative z-20 flex flex-col sm:flex-row justify-between items-center sm:items-start">
      <div className="my-3 mr-auto sm:mr-0">
        <BackLinkButton />
      </div>
      <div className="space-y-6 w-[310px]">
        <h3 className="text-2xl -mb-3 sm:mb-0 font-semibold">Order Summary</h3>
        <ServiceCard
          style={{ width: "100%" }}
          title="1. Muneland Workshop"
          image="https://i.ibb.co/K7NH3ZY/Strategy-Vision-5.png"
          price="70"
          delivery_days="4"
        />
        <div className="space-y-3">
          <h3 className="text-2xl font-semibold">Payment Options</h3>
          <PaymentMethodImage
            method="mastercard"
            image="https://i.ibb.co/7Q21TVh/Mastercard.png"
          />
          <PaymentMethodImage
            method="paypal"
            image="https://i.ibb.co/xHhGFV9/Paypal.png"
          />
          <PaymentMethodImage
            method="visa"
            image="https://i.ibb.co/8X1gMkv/Visa.png"
          />
        </div>
      </div>

      <div className="w-10" />
    </div>
  );
};

export default PaymentLeftbar;
