import React from "react";

const Input48 = ({
  type = "text",
  id,
  name,
  onChange,
  placeholder,
  ...props
}) => {
  return (
    <input
      {...props}
      onChange={onChange}
      className="w-full h-12 px-4 rounded focus:outline-none border bg-gray-50 border-[#E0E0E0] transition-none focus:border-transparent focus:ring"
      type={type}
      id={id || type}
      name={name || id || type}
      placeholder={placeholder}
    />
  );
};

export default Input48;
