import React, { useState } from "react";
import HeaderWithPhoto from "../components/Headers/HeaderWithPhoto";
import OverlayPopup from "../components/Utilities/OverlayPopup";
import { Link } from "react-router-dom";

import Desktop from "../../assets/svgs/desktop.svg";
import Setting from "../../assets/svgs/setting.svg";
import Database from "../../assets/svgs/database.svg";
import Cloud from "../../assets/svgs/cloud.svg";
import CheckSecure from "../../assets/svgs/check-secure.svg";
import LongLine from "../../assets/svgs/long-line.svg";
import ArrowDownload from "../../assets/svgs/arrow-download.svg";
import Times from "../../assets/svgs/times-outline.svg";
import DarkButtonCircle from "../components/Utilities/DarkButtonCircle";
import DarkButtonCircleOutline from "../components/Utilities/DarkButtonCircleOutline";

const StatusScreen = () => {
  const [show_popup, set_show_popup] = useState(false);
  return (
    <>
      <HeaderWithPhoto show_others={true} />

      <div
        style={{ minHeight: "calc(100vh - 75px)" }}
        className="w-full bg-[#F2F2F7] text-[#222] py-8 2xl:py-10"
      >
        <div className="x_container">
          <h3 className="text-2xl 2xl:text-3xl mb-5">
            Status report for Leslies Project
          </h3>
          {/* All Status Boxes */}
          <div className="rounded shadow bg-white">
            <StatusBox
              name="Leslie Alexander"
              sub_title="has started"
              status_text="in progress"
            />
            <StatusBox
              name="Leslie Alexander"
              sub_title="has almost finished"
              status_text="soon ready"
            />
            <StatusBox
              name="Leslie Alexander"
              sub_title="has confimed the delivery"
              status_text="ready"
              arrow_down_icon={true}
            />

            <div className="py-6 px-6 lg:pl-10 flex flex-col sm:flex-row justify-start gap-6 w-full lg:w-8/12">
              <Link to="/deliver-approved" className="block w-full">
                <DarkButtonCircle>Approve</DarkButtonCircle>
              </Link>

              <DarkButtonCircleOutline onClick={() => set_show_popup(true)}>
                I need one more iteration
              </DarkButtonCircleOutline>
            </div>
          </div>
        </div>
      </div>

      <OverlayPopup show_popup={show_popup} set_show_popup={set_show_popup}>
        <div className="w-full sm:w-[600px] p-6 bg-white relative">
          <h3 className="text-xl font-semibold">Iteration Change</h3>
          <textarea
            className="w-full min-h-[250px] p-5 mt-3 mb-4 focus:outline-none focus:ring border rounded"
            placeholder="Type message here..."
          ></textarea>
          <div className="w-full lg:w-8/12">
            <DarkButtonCircle>Send Message</DarkButtonCircle>
          </div>
          <img
            onClick={() => set_show_popup(false)}
            className="w-6 h-6 absolute top-4 right-4 opacity-75 cursor-pointer"
            src={Times}
            alt=""
          />
        </div>
      </OverlayPopup>
    </>
  );
};

export default StatusScreen;

function StatusBox({ name, sub_title, status_text, arrow_down_icon }) {
  return (
    <div className="h-28 flex items-center justify-around border-b">
      <div className="flex items-center gap-1 lg:gap-3 lg:w-64">
        <img
          className="w-8 lg:w-14 2xl:w-16 h-8 lg:h-14 2xl:h-16 rounded-full"
          src="https://randomuser.me/api/portraits/men/36.jpg"
          alt=""
        />
        <div className="flex flex-col items-start">
          <h3 className="text-baes lg:text-xl font-semibold">{name}</h3>
          <span className="text-[#777] text-sm lg:text-base w-32">
            {sub_title}
          </span>
        </div>
      </div>

      <div className="flex items-center gap-1 lg:gap-3">
        <img className="w-2 sm:w-3 lg:w-7" src={Desktop} alt="" />

        <div className="h-fit md:p-0 md:h-7 lg:h-10 w-fit md:w-24 lg:w-36 text-[9px] md:text-sm lg:text-xl font-semibold capitalize">
          <StatusText />
        </div>
      </div>

      <div className="relative">
        <img
          className="w-2 sm:w-3 lg:w-7 absolute inset-x-0 mx-auto bottom-3 lg:bottom-6"
          src={Setting}
          alt=""
        />
        <img className="w-9 sm:w-14 lg:w-24" src={LongLine} alt="" />
      </div>

      <div className="flex gap-2 lg:gap-5">
        <img className="w-2 sm:w-3 lg:w-7" src={Database} alt="" />
        <img className="w-2 sm:w-3 lg:w-7" src={Cloud} alt="" />
        <img className="w-2 sm:w-3 lg:w-7" src={Database} alt="" />
      </div>

      <div className="flex items-center gap-1 lg:gap-3">
        <div className="relative">
          <img
            className="w-2 sm:w-3 lg:w-6 absolute inset-x-0 mx-auto bottom-3 lg:bottom-6"
            src={CheckSecure}
            alt=""
          />
          <img className="w-9 snLw-14 lg:w-24" src={LongLine} alt="" />
        </div>
        <img className="w-2 sm:w-3 lg:w-7" src={Desktop} alt="" />
        {arrow_down_icon ? (
          <button>
            <img className="w-2 sm:w-3 lg:w-7" src={ArrowDownload} alt="" />
          </button>
        ) : (
          <div className="w-2 sm:w-3 lg:w-7" />
        )}
      </div>
    </div>
  );

  function StatusText() {
    if (status_text?.trim()?.toLowerCase() === "ready") {
      return (
        <span className="w-full h-full flex items-center justify-center text-[#27AE60] bg-[#27AE6040] rounded px-1">
          {status_text}
        </span>
      );
    } else if (status_text?.trim()?.toLowerCase() === "soon ready") {
      return (
        <span className="w-full h-full flex items-center justify-center text-[#D2B758] bg-[#D2B75840] rounded px-1">
          {status_text}
        </span>
      );
    } else {
      return (
        <span className="w-full h-full flex items-center justify-center text-[#844FBA] bg-[#844FBA40] rounded px-1">
          {status_text}
        </span>
      );
    }
  }
}
