import React from "react";

import SearchIcon from "../../../assets/svgs/search-icon.svg";
const ChatUsersSearch = () => {
  return (
    <div style={{ height: "100px" }} className="pr-6 flex items-center w-full">
      <div className="relative w-full">
        <label
          htmlFor="search"
          className="absolute inset-y-0 my-auto left-3 flex h-full items-center"
        >
          <img className="w-5" src={SearchIcon} alt="" />
        </label>
        <input
          className="w-full h-14 bg-[#F2F2F7] pl-10 pr-4 rounded shadow focus:outline-none focus:ring-2"
          type="search"
          id="search"
          placeholder="Search Chat"
        />
      </div>
    </div>
  );
};

export default ChatUsersSearch;
