import React, { useEffect, useState } from "react";
import { NavLink as NLink, useLocation } from "react-router-dom";

export const NavLink = ({ to, children }) => {
  const pathnames = useLocation().pathname.split("/");
  const first_pathname = pathnames[1];
  const last_pathname = pathnames[pathnames.length - 1];

  const [active, set_active] = useState(false);

  useEffect(() => {
    if (to === "" && first_pathname === last_pathname) {
      set_active(true);
    } else {
      set_active(false);
    }

    if (to === last_pathname) {
      set_active(true);
    }
  }, [to, first_pathname, last_pathname]);

  return (
    <NLink
      to={to}
      className={({ isActive }) =>
        `${
          isActive && active
            ? "border-[#844FBA] text-[#222]"
            : "border-transparent text-[#959595]"
        } block w-fit lg:w-full py-2 lg:pl-3 border-b-4 lg:border-b-0 lg:border-l-4 text-xl font-semibold`
      }
    >
      {children}
    </NLink>
  );
};

const SidebarLinks = ({ children }) => {
  return (
    <ul className="flex flex-wrap lg:flex-nowrap flex-row lg:flex-col gap-5">
      {children}
    </ul>
  );
};

export default SidebarLinks;
